const MY_BACKEND = process.env.REACT_APP_API_BASE_URL;

export async function register(email, password) {
  const response = await fetch(`${MY_BACKEND}/users/signup`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      email,
      password,
    }),
  });
  if (!response.ok) {
    throw new Error("Error al registrar");
  }

  const data = await response.json();
  return data;
}

export async function login(email, password) {
  const response = await fetch(`${MY_BACKEND}/users/signin`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      email,
      password,
    }),
  });
  if (!response.ok) {
    throw new Error("no se ha proporcionado uno o ms campos");
  }

  const data = await response.json();
  localStorage.setItem("jwt", data.token);
  return data;
}

export async function checkToken(token) {
  const response = await fetch(`${MY_BACKEND}/users/me`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    },
  });
  if (!response.ok) {
    return { error: "token invlido" };
  }

  const data = response.json();
  return data;
}
